import axios from 'axios';

export const login = async (email, password) => {
  try {
    const res = await axios({
      method: 'POST',
      url: 'http://localhost:4200/auth/login',
      data: {
        email,
        password,
      },
      headers: {
        'Content-Type': 'application/json',
      },
    });
    if (res.data.status === 'success') {
      window.setTimeout(() => {
        location.href = '/template';
      }, 1500);
    }
  } catch (error) {
    console.log(error);
  }
};
